import React, { useState, useEffect } from "react";
import MathProblem from "./components/MathProblem";
import DrawingPane from "./components/DrawingPane";
import { createWorker } from "tesseract.js";

const convertImageToText = async (canvas, worker) => {
  const {
    data: { text },
  } = await worker.recognize(canvas, {
    tessedit_char_whitelist: "0123456789",
  });

  const userAttempt = text.trim();
  console.log(userAttempt);
  return userAttempt;
};

const App = () => {
  const [result, setResult] = useState(null);
  const [correctAnswer, setCorrectAnswer] = useState(null);
  const [worker, setWorker] = useState(null);

  useEffect(() => {
    const initWorker = async () => {
      const newWorker = await createWorker();
      await newWorker.load();
      await newWorker.loadLanguage("eng");
      await newWorker.initialize("eng");
      setWorker(newWorker);
    };

    initWorker();

    return () => {
      if (worker) {
        worker.terminate();
      }
    };
  }, []);

  const handleSubmit = async () => {
    if (!worker) return;

    const canvas = document.querySelector("canvas");
    const answer = await convertImageToText(canvas, worker);

    if (parseInt(answer) === correctAnswer) {
      setResult("Correct");
    } else {
      setResult("Incorrect");
    }
    canvas.getContext("2d").clearRect(0, 0, canvas.width, canvas.height);
  };

  return (
    <div>
      <MathProblem setCorrectAnswer={setCorrectAnswer} />
      <DrawingPane />
      <button onClick={handleSubmit}>Submit</button>
      <p>{result}</p>
    </div>
  );
};

export default App;
